* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Arial, Helvetica, sans-serif;
}

input, button {
    appearance: none;
    outline: none;
    border: none;
    background: none;
}


form {
    display: block;
    position: relative;
}

form:after {
    content: '';
    display: block;
    position: absolute;
    top: -5px;
    left: -5px;
    top: -5px;
    bottom: -5px;
    z-index: 1;
    background-image: linear-gradient(to bottom right, #FFCE00, #FE4880);
}

form .form-inner {
    position: relative;
    display: block;
    background-color: #FFF;
    padding: 30px;
    z-index: 2;
}

form .form-inner h2 {
    color: #888;
    font-size: 28px;
    font-weight: 500;
    margin-bottom: 30px;
}

form .form-inner .form-group {
    display: block;
    width: 300px;
    margin-bottom: 20px;
}

.form-inner .form-group label {
    display: block;
    color: #888;
    font-size: 14px;
    margin-bottom: 5px;
    transition: 0.4s;
}

.form-inner .form-group:focus-within label {
    color: #FE4880;
}   

form .form-inner .form-group input {
    display: block;
    width: 100%;
    padding: 10px 15px;
    border-radius: 8px;
    background-color: #F8F8F8;
    transition: 0.4s;
}
form .form-inner .form-group input:focus {
    box-shadow: 0px 0px 3px rgba(0,0,0,0.2);
} 

form .form-inner input[type="submit"], 
.welcome button {
    display: block;
    width: 100%;
    padding: 10px 15px;
    border-radius: 8px;
    background-color: #FFCE00;
    color: #000;
    font-size: 16px;
    font-weight: 500;
    transition: 0.4s;
}

form .form-inner input[type="submit"]:hover,
.welcome button:hover {
    background-position: 100% 0%;
}

.welcome {
    width: 100%;
    max-width: 480px;
    background-color: #FFF;
    padding: 15px 30px;
}

.welcome h2 {
    color: #888;
    font-size: 28px;
    font-weight: 500;
    margin-bottom: 30px;
}

.welcome h2 span {
    color: #FE4880;
    font-weight: 700;
}

